.page {
  scroll-behavior: smooth;
  background-color: #f2f6fa;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__body {
  min-width: 320px;
  margin: 0;
}

.page__container {
  margin: 0 auto;
  padding-inline: 21px;
}

@media (width >= 640px) {
  .page__container {
    padding-inline: 41px;
  }
}

@media (width >= 1024px) {
  .page__container {
    padding-inline: 54px;
  }
}

@media (width >= 1600px) {
  .page__container {
    max-width: 1148px;
  }
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page__mobile-hidden {
  display: none;
}

@media (width >= 640px) {
  .page__mobile-hidden {
    display: inline-block;
